body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.encabezado{
display: flex;
flex-flow: row wrap;
justify-content: space-between;
background-color: black;
padding: 1.5em;
color: white;
}

.encabezado h1{
  font-weight: 500;
  font-size: 2em;
}

.nUsuario{
  margin-left: 38em;
  margin-top: 2em;
}

.imagen{
  width: 4.5em;
  margin-top: 1em;
}

.section-parrafo p{
  padding: 0.5em 2em;
  height: 3em;
  
}

/* ---************-------------------tabla---------------------------****************----- */





.contenedorTabla{
  margin: 1em;

}

.tabla{
  border: 0.2em solid rgb(206, 31, 31);
  width: 100%;
  
}

.tablaCabeza{
  border: 0.2em solid rgb(106, 141, 255);
  margin: 0.2em;

}

.tablaCabeza th{
height: 3em;
display: flex;
align-items: center;


 
}

.contenedorThead{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 0.5em;
  
}

/* ----------------------- contenedor table body ---------------------- */

.tbody td{
height: 2em;
width: 50%;
padding: 0.8em;

  
}

.tbody{
  border: 0.2em solid rgb(32, 151, 72);
  margin: 0.2em;
  
}

.botonTbody{
  border: orange solid 0.2em;
}


/* ---------------------footer--------------------- */

.el-footer{
  background-color: black;
  height:3.3em;
  
  text-align: center;
}
.el-footer p{
  color: white;
  padding-top: 1em;
}